class shipRandomizer {
  GameBoard;
  constructor(GameBoard) {
    this.GameBoard = GameBoard;
  }
  placeShip() {
    this.generate4costs();
    this.generate3costs();
    this.generate2costs();
    this.generate1costs();
  }
  generate4costs() {
    let side = this.randomIntFromInterval(0, 1);
    while (true) {
      if (side) {
        let xBegin = this.randomIntFromInterval(0, 9);
        let xEnd = xBegin + 1;
        let yBegin = this.randomIntFromInterval(0, 6);
        let yEnd = yBegin + 4;
        if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
          break;
        }
      } else {
        let yBegin = this.randomIntFromInterval(0, 9);
        let yEnd = yBegin + 1;
        let xBegin = this.randomIntFromInterval(0, 6);
        let xEnd = xBegin + 4;
        if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
          break;
        }
      }
    }
  }
  generate3costs() {
    let side = this.randomIntFromInterval(0, 1);
    for (let i = 0; i < 2; i++) {
      while (true) {
        if (side) {
          let xBegin = this.randomIntFromInterval(0, 9);
          let xEnd = xBegin + 1;
          let yBegin = this.randomIntFromInterval(0, 7);
          let yEnd = yBegin + 3;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        } else {
          let yBegin = this.randomIntFromInterval(0, 9);
          let yEnd = yBegin + 1;
          let xBegin = this.randomIntFromInterval(0, 7);
          let xEnd = xBegin + 3;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        }
      }
    }
  }
  generate2costs() {
    let side = this.randomIntFromInterval(0, 1);
    for (let i = 0; i < 3; i++) {
      while (true) {
        if (side) {
          let xBegin = this.randomIntFromInterval(0, 9);
          let xEnd = xBegin + 1;
          let yBegin = this.randomIntFromInterval(0, 8);
          let yEnd = yBegin + 2;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        } else {
          let yBegin = this.randomIntFromInterval(0, 9);
          let yEnd = yBegin + 1;
          let xBegin = this.randomIntFromInterval(0, 8);
          let xEnd = xBegin + 2;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        }
      }
    }
  }
  generate1costs() {
    let side = this.randomIntFromInterval(0, 1);
    for (let i = 0; i < 4; i++) {
      while (true) {
        if (side) {
          let xBegin = this.randomIntFromInterval(0, 9);
          let xEnd = xBegin + 1;
          let yBegin = this.randomIntFromInterval(0, 9);
          let yEnd = yBegin + 1;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        } else {
          let yBegin = this.randomIntFromInterval(0, 9);
          let yEnd = yBegin + 1;
          let xBegin = this.randomIntFromInterval(0, 9);
          let xEnd = xBegin + 1;
          if (this.GameBoard.createShip(xBegin, yBegin, xEnd, yEnd)) {
            break;
          }
        }
      }
    }
  }
  randomIntFromInterval(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  checkInRange(input) {
    let result = input + 4;
    if (result >= 0 && result <= 10) {
      return result;
    } else {
      return result - 9;
    }
  }
}
export default shipRandomizer;
