import Player from "./Player";
class Computer extends Player {
  hitPoints;
  gameBoard;
  constructor(name, gameBoard) {
    super(name);
    this.hitPoints = [];
  }
  setGameBoard(gameBoard) {
    this.gameBoard = gameBoard;
  }
  setController(controller) {
    this.controller = controller;
  }
  hit() {
    let points = this.randomizePoint();
    let cells = document.querySelectorAll(".player1>div");
    cells.forEach((cell) => {
      if (points[0] == cell.dataset.x && points[1] == cell.dataset.y) {
        this.hitPoints.push(points);
        cell.click();
      }
    });
  }
  randomizePoint() {
    let result;
    while (true) {
      let points = [this.randomIntFromInterval(), this.randomIntFromInterval()];
      if (!this.checkDuplicate(points)) {
        result = points;
        break;
      }
    }
    return result;
  }
  checkDuplicate(points) {
    let result = false;
    this.hitPoints.forEach((hitPoint) => {
      if (hitPoint[0] == points[0] && hitPoint[1] == points[1]) {
        result = true;
      }
    });
    return result;
  }
  randomIntFromInterval(min = 0, max = 9) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
}
export default Computer;
