___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: antiquewhite;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.container {
  width: 1000px;
  height: 500px;
  background-color: #eee;
  display: flex;
  align-content: space-around;
  gap: 2%;
  user-select: none;
}
.container > div {
  width: 50%;
  height: 100%;
  background-color: rgb(19, 41, 116);
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  padding: 2px;
  gap: 2px;
  box-shadow: 10px 10px 2px #525252;
}
.cell {
  background-color: #fabd7f;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.cell:hover {
  background-color: #eee;
}
.cell.ship {
  background-color: #65adce;
}
.cell.missed {
  background-color: #525252;
}
.cell.hit {
  background-color: #ff7e7e;
}
.cell.stuck {
  background-color: #651d7a;
}
.cell.hide {
  background-color: #f5f5dc;
}
.board {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
#reset {
  padding: 5px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background-color: #65adce;
}
