class StatusRender {
  controller;
  functionButtons;
  constructor(controller) {
    this.controller = controller;
    this.functionButtons = document.querySelector(".setting");
    this.resetButton();
  }
  resetButton() {
    let resetButton = this.functionButtons.querySelector("#reset");
    resetButton.addEventListener("click", () => {
      this.controller.resetGame();
    });
  }
}
export default StatusRender;
