import Player from "../assets/Player";
import Computer from "../assets/Computer";
import GameBoard from "../assets/Gameboard";
import BoardRender from "./BoardRender";
import shipRandomizer from "../assets/ShipRandomizer";
import StatusRender from "./StatusRender";
class Control {
  turn;
  Player1;
  Player2;
  BoardRenderInstance1;
  BoardRenderInstance2;
  GameBoard1;
  GameBoard2;
  Winner;
  constructor(Player1, Player2) {
    this.Player1 = Player1;
    this.Player2 = Player2;
    this.initGame();
  }

  initGame() {
    this.GameBoard1 = new GameBoard();
    this.GameBoard2 = new GameBoard();
    const StatusRender1 = new StatusRender(this);

    this.Player2.setGameBoard(this.GameBoard1);
    this.Player2.setController(this);

    const shipRandomize1 = new shipRandomizer(this.GameBoard1);
    const shipRandomize2 = new shipRandomizer(this.GameBoard2);

    this.BoardRenderInstance1 = new BoardRender(this);
    this.BoardRenderInstance2 = new BoardRender(this);

    shipRandomize1.placeShip();
    shipRandomize2.placeShip();

    this.BoardRenderInstance1.render(this.GameBoard1, this.Player1.name);
    this.BoardRenderInstance2.render(this.GameBoard2, this.Player2.name);
    this.turn = "Player1";
    this.BoardRenderInstance1.openMap();
  }

  updateGameState(state) {
    if (state == true) {
      this.continueTurn();
    } else {
      this.switchTurn();
    }
    this.checkWinner();
  }
  continueTurn() {
    if (this.turn == "Player1") {
    } else if (this.turn == "Player2") {
      this.player2Turn();
    }
  }
  switchTurn() {
    if (this.turn == "Player1") {
      this.turn = "Player2";
      this.player2Turn();
    } else if (this.turn == "Player2") {
      this.turn = "Player1";
    }
  }
  player2Turn() {
    this.Player2.hit();
  }
  checkWinner() {
    if (this.GameBoard1.checkAllSunk()) {
      this.Winner = "player 2";
      this.endGame();
    }
    if (this.GameBoard2.checkAllSunk()) {
      this.Winner = "player 1";
      this.endGame();
    }
  }
  endGame() {
    alert(`${this.turn} is win`);
    this.resetGame();
  }
  resetGame() {
    // computer hit gameBoard1
    this.initGame();
  }
}
const player1 = new Player("player1");
const computer2 = new Computer("player2");
const controller = new Control(player1, computer2);

export default Control;
