import Ship from "./Ship";
class GameBoard {
  width;
  height;
  closer;
  previousHits;
  hits;
  stuckSpots;
  misses;
  shipList;
  constructor() {
    this.width = 10;
    this.height = 10;
    this.closer = [];
    this.previousHits = [];
    this.hits = [];
    this.stuckSpots = [];
    this.misses = [];
    this.shipList = [];
  }

  createShip(xBegin, yBegin, xEnd, yEnd) {
    if (xBegin > xEnd) {
      xEnd = [xBegin, (xBegin = xEnd)][0];
    }
    if (yBegin > yEnd) {
      yEnd = [yBegin, (yBegin = yEnd)][0];
    }
    if (this.#checkValid(xBegin, yBegin, xEnd, yEnd)) {
      return false;
    }
    let length = this.#shipLength(xBegin, yBegin, xEnd, yEnd);
    const shipInstance = new Ship(length);
    let ATField = this.#createATField(xBegin, yBegin, xEnd, yEnd);

    let shipInform = {
      ship: shipInstance,
      xBegin: xBegin,
      yBegin: yBegin,
      xEnd: xEnd,
      yEnd: yEnd,
      ATField: ATField,
    };
    this.shipList.push(shipInform);
    return true;
  }
  #shipLength(xBegin, yBegin, xEnd, yEnd) {
    if (Math.abs(xBegin - xEnd) == 1) {
      return Math.abs(yBegin - yEnd);
    } else {
      return Math.abs(xBegin - xEnd);
    }
  }
  #checkValid(...input) {
    if (this.#checkInBound(input) && !this.#checkCrossShip(input)) {
      return false;
    } else {
      return true;
    }
  }
  #checkInBound(input) {
    let result = true;
    input.forEach((element) => {
      if (element > 10 && element < 0) {
        result = false;
      }
    });
    return result;
  }
  #checkCrossShip(input) {
    let result = false;
    let newShipPoints = this.#shipPoints(
      input[0],
      input[1],
      input[2],
      input[3]
    );
    newShipPoints.forEach((shipPoint) => {
      this.shipList.forEach((ship) => {
        ship.ATField.forEach((ATPoint) => {
          if (shipPoint[0] == ATPoint[0] && shipPoint[1] == ATPoint[1]) {
            result = true;
          }
        });
        this.#shipPoints(
          ship.xBegin,
          ship.yBegin,
          ship.xEnd,
          ship.yEnd
        ).forEach((point) => {
          if (shipPoint[0] == point[0] && shipPoint[1] == point[1]) {
            result = true;
          }
        });
      });
    });
    return result;
  }
  #shipPoints(xBegin, yBegin, xEnd, yEnd) {
    let result = [];

    if (xEnd - xBegin == 1) {
      for (let i = yBegin; i < yEnd; i++) {
        result.push([xBegin, i]);
      }
    } else {
      for (let i = xBegin; i < xEnd; i++) {
        result.push([i, yBegin]);
      }
    }
    return result;
  }
  getHits(X, Y) {
    let result = false;
    if (this.#inPreviousHit(X, Y)) {
      return result;
    }
    this.shipList.forEach((shipInform) => {
      if (
        X >= shipInform.xBegin &&
        X < shipInform.xEnd &&
        Y >= shipInform.yBegin &&
        Y < shipInform.yEnd
      ) {
        shipInform.ship.hit();
        result = true;
      }
    });
    if (result) {
      this.hits.push([X, Y]);
      this.#checkStuckAndShipPoint(X, Y).forEach((element) => {
        this.stuckSpots.push(element);
      });
      this.#coverSunkShip(X, Y);
    } else {
      this.misses.push([X, Y]);
    }
    return result;
  }

  #createATField(xBegin, yBegin, xEnd, yEnd) {
    let result = [];
    if (xEnd - xBegin == 1) {
      result.push([xBegin, yBegin - 1]);
      result.push([xBegin, yEnd]);
      for (let y = yBegin - 1; y <= yEnd; y++) {
        result.push([xBegin - 1, y]);
        result.push([xEnd, y]);
      }
    } else {
      result.push([xBegin - 1, yBegin]);
      result.push([xEnd, yBegin]);
      for (let x = xBegin - 1; x <= xEnd; x++) {
        result.push([x, yBegin - 1]);
        result.push([x, yEnd]);
      }
    }
    return result;
  }

  #inPreviousHit(X, Y) {
    let result = false;

    this.hits.forEach((element) => {
      if (element[0] == X && element[1] == Y) {
        result = true;
      }
    });
    this.misses.forEach((element) => {
      if (element[0] == X && element[1] == Y) {
        result = true;
      }
    });
    return result;
  }
  #checkStuckAndShipPoint(X, Y) {
    let points = [
      [X - 1, Y - 1],
      [X - 1, +Y + 1],
      [+X + 1, Y - 1],
      [+X + 1, +Y + 1],
    ];
    return points;
  }
  #coverSunkShip(X, Y) {
    this.shipList.forEach((shipInform) => {
      if (
        X >= shipInform.xBegin &&
        X < shipInform.xEnd &&
        Y >= shipInform.yBegin &&
        Y < shipInform.yEnd
      ) {
        let ATField = shipInform.ATField;
        let exception = [];
        if (shipInform.ship.isSunk()) {
          ATField.forEach((ATPoint) => {
            let duplicate = false;
            this.stuckSpots.forEach((stuckSpot) => {
              if (stuckSpot[0] == ATPoint[0] && stuckSpot[1] == ATPoint[1]) {
                duplicate = true;
              }
            });
            !duplicate && exception.push(ATPoint);
          });
        }
        exception.forEach((item) => this.stuckSpots.push(item));
      }
    });
  }
  checkAllSunk() {
    let result = true;
    this.shipList.forEach((ship) => {
      if (ship.ship.sunk == false) {
        result = false;
        return;
      }
    });
    return result;
  }
}
export default GameBoard;
